import React, {Component} from 'react';
import '../App.css';
import { Nav, NavItem, NavLink } from 'reactstrap';
import Typist from 'react-typist';
import ReactDOM from 'react-dom';


export default class Interests extends React.Component {

  constructor(props) {
      super(props);

      this.state = {
          isToggleOn: false
      };

      this.handleClick = this.handleClick.bind(this);
  }

  // On this click we will want to render a full Navbar with desired links 
  handleClick() {

      // Add Stuff to DOM
      if (!this.state.isToggleOn) {
          ReactDOM.render(this.workList(), document.getElementById('interestlist'));

      }

      else {
          ReactDOM.unmountComponentAtNode(document.getElementById('interestlist'));
      }

      this.setState({isToggleOn: !this.state.isToggleOn});
  }

  // Alter this list to display desired stuff
  workList() {
      return (
        <Nav vertical className = "workNav">
          <code className = "prof">Below are some of my hobbies and the things I like to do for fun.</code>
          <br></br>
          <NavItem>
            <NavLink className = "static"><code>üìà Hobby <a href = "https://boardgamegeek.com/" className = "linkElem" target = "_blank">Boardgames</a>. Heavy Economic/European strategy boardgames are my preference, but I'll try almost any game once.</code></NavLink>
          </NavItem>
          <br></br>
          <NavItem>
            <NavLink className = "static"><code>üéµ Music. Preferrably <a className = "linkElem" href ="https://www.youtube.com/watch?v=Rb0UmrCXxVA" target = "_blank">Classical and Mozart</a> or <a className = "linkElem" href = "https://www.youtube.com/playlist?list=PLE774335E942D5938" target = "_blank">Kanye West.</a></code></NavLink>
          </NavItem>
          <br></br>
          <NavItem>
            <NavLink className = "static"><code>üì± Tech. Interested in all new technology, especially mobile. <a className = "linkElem" href = "https://www.apple.com/" target = "_blank">Apple</a> fan first.</code></NavLink>
          </NavItem>
          <br></br>
          <NavItem>
            <NavLink className = "static"><code>üçî Food. Trying new Restaurants and Coffee Places wherever I am.</code></NavLink>
          </NavItem>
          <br></br>
          <NavItem>
            <NavLink className = "static"><code>üèÄ Basketball. I'll play pickup whenever I can.</code></NavLink>
          </NavItem>
          <br></br>
        </Nav>
      );
  }



  render() {
      return (
          <Nav vertical className = "nav">
          <NavItem onClick = {this.handleClick} className = "listElem">
              <Typist>
                <code>üëå Interests.</code>
              </Typist>
          </NavItem>
        </Nav>
      );
  }
}

